<template>
  <div>
    <v-stepper v-model="e1" non-linear class="mt-6">
      <v-stepper-header>
        <v-stepper-step editable :complete="e1 > 4" step="1" :color="color">
          Account Opening
        </v-stepper-step>

        <v-divider></v-divider>

        <v-stepper-step
          :editable="editSteppers"
          :complete="e1 > 4"
          step="2"
          :color="color"
        >
          Maternal Demographics
        </v-stepper-step>

        <v-divider></v-divider>

        <v-stepper-step
          :editable="editSteppers"
          :complete="e1 > 4"
          step="3"
          :color="color"
        >
          Maternal Obstetric History and Paternal History
        </v-stepper-step>

        <v-divider></v-divider>

        <v-stepper-step
          :editable="editSteppers"
          :complete="e1 > 4"
          step="4"
          :color="color"
        >
          Baby's Data
        </v-stepper-step>
      </v-stepper-header>

      <v-stepper-items>
        <v-stepper-content step="1">
          <h3 v-show="mobile" class=" text-center mb-2">Account Opening</h3>
          <v-card class="mb-7" :color="cardColor">
            <v-container>
              <v-form>
                <v-textarea
                  label="Name:"
                  v-model="dForm.name"
                  :color="color"
                  :rules="[rules.required]"
                  :readonly="!!dForm.id && !edit.name"
                  auto-grow
                  rows="1"
                >
                  <template v-if="dForm.id" v-slot:append>
                    <v-btn icon @click="edit.name = !edit.name">
                      <v-icon v-if="edit.name">mdi-close</v-icon>
                      <v-icon v-else>mdi-pencil</v-icon>
                    </v-btn>
                  </template>
                </v-textarea>
                <v-text-field
                  label="Hospital Number:"
                  :color="color"
                  v-model="dForm.hosp_no"
                  :rules="[rules.required]"
                  :readonly="!!dForm.id && !edit.hosp_no"
                >
                  <template v-if="dForm.id" v-slot:append>
                    <v-btn icon @click="edit.hosp_no = !edit.hosp_no">
                      <v-icon v-if="edit.hosp_no">mdi-close</v-icon>
                      <v-icon v-else>mdi-pencil</v-icon>
                    </v-btn>
                  </template>
                </v-text-field>
              </v-form>
            </v-container>
          </v-card>
          <v-card-actions>
            <v-btn small color="error">
              Cancel
            </v-btn>
            <v-spacer></v-spacer>
            <v-btn
              small
              v-if="edit.name || edit.hosp_no"
              color="green lighten-1"
              @click="
                updateAccount(
                  1,
                  edit.name ? 'name' : edit.hosp_no ? 'hosp_no' : ''
                )
              "
              :loading="loading"
            >
              Save Changes
            </v-btn>
            <v-btn
              v-else
              small
              color="green lighten-1"
              @click="createAccount"
              :loading="loading"
              :disabled="!!dForm.id"
            >
              Create Account
            </v-btn>
          </v-card-actions>
        </v-stepper-content>

        <v-stepper-content step="2">
          <h3 v-show="mobile" class=" text-center mb-2">
            Maternal Demographics
          </h3>
          <v-card class="mb-8" :color="cardColor">
            <v-container>
              <v-form>
                <v-row>
                  <v-col cols="12" md="6">
                    <v-textarea
                      v-model="dForm.mat_demo.address"
                      :color="color"
                      auto-grow
                      rows="1"
                      label="Address:"
                    ></v-textarea>
                  </v-col>
                  <v-col cols="12" md="6">
                    <v-text-field
                      label="Contact Phone Number on Case Note:"
                      v-model="dForm.mat_demo.phoneNo"
                      :color="color"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6">
                    <v-text-field
                      label="Mother's Age (years):"
                      type="number"
                      v-model="dForm.mat_demo.age"
                      :color="color"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6">
                    <v-text-field
                      label="Mother's Parity:"
                      v-model="dForm.mat_demo.parity"
                      :color="color"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6">
                    <v-select
                      :items="setupData.education"
                      label="Maternal Highest Level of Education:"
                      v-model="dForm.mat_demo.matEdu"
                      :color="color"
                      item-color="success"
                    ></v-select>
                  </v-col>
                  <v-col cols="12" sm="6">
                    <v-text-field
                      label="Mother's Occupation:"
                      v-model="dForm.mat_demo.occupation"
                      :color="color"
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-form>
            </v-container>
          </v-card>
          <v-card-actions>
            <v-btn color="error" small>
              Cancel
            </v-btn>
            <v-spacer></v-spacer>
            <v-btn
              small
              color="green lighten-1"
              @click="updateAccount(3, 'mat_demo')"
            >
              Save and Continue
            </v-btn>
          </v-card-actions>
        </v-stepper-content>

        <v-stepper-content step="3">
          <h3 v-show="mobile" class=" text-center mb-2">
            Maternal Obstetric History and Paternal History
          </h3>
          <v-card class="mb-8" :color="cardColor">
            <v-container>
              <v-form>
                <v-textarea
                  v-model="dForm.mat_pat_hist.matPregCond"
                  :color="color"
                  auto-grow
                  rows="1"
                  label="Maternal Medical Conditions in Pregnancy:"
                ></v-textarea>
                <v-row>
                  <v-col cols="12" sm="6">
                    <v-radio-group
                      dense
                      v-model="dForm.mat_pat_hist.prevPregLoss"
                      row
                    >
                      <span class="mr-3">Previous Pregnancy Loss: </span>
                      <v-radio
                        label="Yes"
                        value="Yes"
                        color="success"
                      ></v-radio>
                      <v-radio label="No" value="No" color="success"></v-radio>
                    </v-radio-group>
                  </v-col>
                  <v-col cols="12" sm="6">
                    <v-radio-group
                      dense
                      v-model="dForm.mat_pat_hist.prevNeonatalDeath"
                      row
                    >
                      <span class="mr-3">Previous Neonatal Death: </span>
                      <v-radio
                        label="Yes"
                        value="Yes"
                        color="success"
                      ></v-radio>
                      <v-radio label="No" value="No" color="success"></v-radio>
                    </v-radio-group>
                  </v-col>
                  <v-col cols="12" sm="6">
                    <v-textarea
                      v-model="dForm.mat_pat_hist.matIllness"
                      :color="color"
                      auto-grow
                      rows="1"
                      label="Maternal Illness on Admission:"
                    ></v-textarea>
                  </v-col>
                  <v-col cols="12" sm="6">
                    <v-radio-group dense v-model="dForm.mat_pat_hist.anc" row>
                      <span class="mr-3">ANC: </span>
                      <v-radio
                        label="Yes"
                        value="Yes"
                        color="success"
                      ></v-radio>
                      <v-radio label="No" value="No" color="success"></v-radio>
                    </v-radio-group>
                  </v-col>
                  <v-col
                    v-if="dForm.mat_pat_hist.anc == 'Yes'"
                    cols="12"
                    sm="6"
                  >
                    <v-radio-group
                      dense
                      v-model="dForm.mat_pat_hist.ancFourVisits"
                      row
                    >
                      <span class="mr-3">ANC upto Four Visits: </span>
                      <v-radio
                        label="Yes"
                        value="Yes"
                        color="success"
                      ></v-radio>
                      <v-radio label="No" value="No" color="success"></v-radio>
                    </v-radio-group>
                  </v-col>
                  <v-col
                    v-if="dForm.mat_pat_hist.anc == 'Yes'"
                    cols="12"
                    sm="6"
                  >
                    <v-select
                      :items="setupData.healthcare1"
                      label="Place of ANC"
                      v-model="dForm.mat_pat_hist.ancPlace"
                      item-text="place"
                      item-value="abbr"
                      :color="color"
                      item-color="success"
                    ></v-select>
                  </v-col>
                  <v-col cols="12" sm="6">
                    <v-select
                      :items="setupData.healthcare2"
                      label="Place of Delivery"
                      v-model="dForm.mat_pat_hist.deliveryPlace"
                      item-text="place"
                      item-value="abbr"
                      :color="color"
                      item-color="success"
                    >
                    </v-select>
                  </v-col>
                  <v-col
                    v-if="dForm.mat_pat_hist.deliveryPlace == 'Others'"
                    cols="12"
                    sm="6"
                    md="4"
                  >
                    <v-textarea
                      v-model="dForm.mat_pat_hist.deliveryPlaceSpecified"
                      :color="color"
                      auto-grow
                      rows="1"
                      label="Specify Other Delivery Place:"
                    ></v-textarea>
                  </v-col>

                  <v-col cols="12" sm="6">
                    <v-select
                      :items="setupData.deliveryMode"
                      label="Mode of Delivery:"
                      v-model="dForm.mat_pat_hist.deliveryMode"
                      item-text="mode"
                      item-value="abbr"
                      :color="color"
                      item-color="success"
                    ></v-select>
                  </v-col>
                  <v-col cols="12" sm="6" v-if="csDelivery">
                    <v-textarea
                      v-model="dForm.mat_pat_hist.csIndication"
                      :color="color"
                      auto-grow
                      rows="1"
                      label="C/S indication:"
                    ></v-textarea>
                  </v-col>
                  <v-col cols="12" sm="6">
                    <v-text-field
                      label="Father's Age(years):"
                      type="number"
                      v-model="dForm.mat_pat_hist.patAge"
                      :color="color"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6">
                    <v-select
                      :items="setupData.education"
                      label="Father's Highest Level of Education:"
                      v-model="dForm.mat_pat_hist.patEdu"
                      :color="color"
                      item-color="success"
                    ></v-select>
                  </v-col>
                  <v-col cols="12" sm="6">
                    <v-text-field
                      label="Father's Occupation:"
                      v-model="dForm.mat_pat_hist.patOccupation"
                      :color="color"
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-form>
            </v-container>
          </v-card>
          <v-card-actions>
            <v-btn small color="error">
              Cancel
            </v-btn>
            <v-spacer></v-spacer>
            <v-btn
              small
              color="green lighten-1"
              @click="updateAccount(4, 'mat_pat_hist')"
            >
              Save and Continue
            </v-btn>
          </v-card-actions>
        </v-stepper-content>

        <v-stepper-content step="4">
          <h3 v-show="mobile" class=" text-center mb-2">Baby's Data</h3>
          <v-row>
            <v-col>
              <v-card :color="cardColor">
                <v-card-title>
                  <v-row>
                    <v-item-group v-model="window" mandatory>
                      <v-item
                        v-for="(n, m) in buttons"
                        :key="m"
                        v-slot="{ active, toggle }"
                      >
                        <v-btn
                          class="mx-2"
                          x-small
                          color="light-green lighten-2"
                          :input-value="active"
                          @click="toggle"
                        >
                          {{ n }}
                        </v-btn>
                      </v-item>
                    </v-item-group>
                  </v-row>
                </v-card-title>
                <v-window v-model="window">
                  <v-container>
                    <v-window-item :value="0">
                      <v-row>
                        <v-col cols="12" sm="6">
                          <v-text-field
                            type="date"
                            label="Date of Birth:"
                            :color="color"
                            v-model="dForm.baby_data.dob"
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6">
                          <v-text-field
                            type="date"
                            label="Date of Admission:"
                            :color="color"
                            v-model="dForm.baby_data.doa"
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6">
                          <v-radio-group
                            dense
                            v-model="dForm.baby_data.sex"
                            name="gender"
                            row
                          >
                            <span class="mr-3">Sex: </span>
                            <v-radio
                              label="Male"
                              value="Male"
                              color="success"
                            ></v-radio>
                            <v-radio
                              label="Female"
                              value="Female"
                              color="success"
                            ></v-radio>
                            <v-radio
                              label="DSD"
                              value="DSD"
                              color="success"
                            ></v-radio>
                          </v-radio-group>
                        </v-col>
                        <v-col cols="12" sm="6">
                          <v-text-field
                            label="Gestational Age (weeks):"
                            type="number"
                            :color="color"
                            v-model="dForm.baby_data.gestationAge"
                          ></v-text-field>
                        </v-col>
                      </v-row>
                      <v-row>
                        <v-col cols="12" sm="10" md="8">
                          <v-text-field
                            label="Age at Admission (hours/days if older than 72 hrs):"
                            type="number"
                            :color="color"
                            v-model="dForm.baby_data.admissionAge"
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="2" md="4">
                          <v-select
                            :items="setupData.units"
                            label="Unit:"
                            v-model="dForm.baby_data.admissionAgeUnit"
                            :color="color"
                            item-color="success"
                          >
                          </v-select>
                        </v-col>
                      </v-row>
                      <v-row>
                        <v-col cols="12" sm="10" md="8">
                          <v-text-field
                            label="Time duration between onset of illness to presentation in facility:"
                            type="number"
                            :color="color"
                            v-model="dForm.baby_data.illnessDuration"
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="2" md="4">
                          <v-select
                            :items="setupData.units"
                            label="Unit:"
                            v-model="dForm.baby_data.illnessDurationUnit"
                            :color="color"
                            item-color="success"
                          >
                          </v-select>
                        </v-col>
                      </v-row>
                      <v-row>
                        <v-col cols="12" sm="6">
                          <v-text-field
                            label="Birth Weight(grams):"
                            type="number"
                            :color="color"
                            v-model="dForm.baby_data.birthWeight"
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6">
                          <v-text-field
                            label="Weight at Presentation(grams):"
                            type="number"
                            :color="color"
                            v-model="dForm.baby_data.presentationWeight"
                          ></v-text-field>
                        </v-col>
                      </v-row>
                    </v-window-item>

                    <v-window-item :value="1">
                      <h3>Patients Examination</h3>
                      <v-row>
                        <v-col cols="12" sm="6" md="4">
                          <v-select
                            :items="setupData.complaints"
                            label="Presenting Complaints:"
                            v-model="dForm.baby_data.presentingComplaints"
                            :color="color"
                            item-color="success"
                            multiple
                          >
                          </v-select>
                        </v-col>
                        <v-col cols="12" sm="6">
                          <v-textarea
                            v-model="
                              dForm.baby_data.presentingComplaintsSpecified
                            "
                            :color="color"
                            auto-grow
                            rows="1"
                            label="Specify Other Complaints:"
                          ></v-textarea>
                        </v-col>

                        <v-col cols="12" sm="6" md="4">
                          <v-select
                            :items="setupData.exams"
                            label="General Examination:"
                            v-model="dForm.baby_data.generalExams"
                            :color="color"
                            item-color="success"
                            multiple
                          >
                          </v-select>
                        </v-col>
                        <v-col cols="12" sm="6">
                          <v-textarea
                            v-model="dForm.baby_data.generalExamsSpecified"
                            :color="color"
                            auto-grow
                            rows="1"
                            label="Specify Other Examinations:"
                          ></v-textarea>
                        </v-col>

                        <v-col cols="12" sm="6" md="4">
                          <v-text-field
                            type="number"
                            :color="color"
                            v-model="dForm.baby_data.admissionSpo2"
                          >
                            <template v-slot:label>
                              SPO<sub>2</sub> at Admission (%):
                            </template>
                          </v-text-field>
                        </v-col>

                        <v-col cols="12" sm="6" md="4">
                          <v-text-field
                            label="OFC(cm):"
                            type="number"
                            :color="color"
                            v-model="dForm.baby_data.ofc"
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field
                            label="Length(cm):"
                            type="number"
                            :color="color"
                            v-model="dForm.baby_data.ofcLength"
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-radio-group
                            dense
                            v-model="dForm.baby_data.headLag"
                            row
                          >
                            <span class="mr-3">Head Lag: </span>
                            <v-radio
                              label="Yes"
                              value="Yes"
                              color="success"
                            ></v-radio>
                            <v-radio
                              label="No"
                              value="No"
                              color="success"
                            ></v-radio>
                          </v-radio-group>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-radio-group
                            dense
                            v-model="dForm.baby_data.suck"
                            row
                          >
                            <span class="mr-3">Suck: </span>
                            <v-radio
                              label="Present"
                              value="Present"
                              color="success"
                            ></v-radio>
                            <v-radio
                              label="Absent"
                              value="Absent"
                              color="success"
                            ></v-radio>
                          </v-radio-group>
                        </v-col>

                        <v-col cols="12" sm="6" md="4">
                          <v-radio-group
                            dense
                            v-model="dForm.baby_data.respiratoryDistress"
                            row
                          >
                            <span class="mr-3">Respiratory Distress: </span>
                            <v-radio
                              label="Yes"
                              value="Yes"
                              color="success"
                            ></v-radio>
                            <v-radio
                              label="No"
                              value="No"
                              color="success"
                            ></v-radio>
                          </v-radio-group>
                        </v-col>
                        <v-col
                          v-if="dForm.baby_data.respiratoryDistress == 'Yes'"
                          cols="12"
                          sm="6"
                          md="4"
                        >
                          <v-select
                            :items="setupData.respitoryEvidence"
                            label="Respitory Evidence:"
                            v-model="dForm.baby_data.respitoryEvidence"
                            :color="color"
                            item-color="success"
                            multiple
                          >
                          </v-select>
                        </v-col>
                        <v-col cols="12" sm="6">
                          <v-textarea
                            v-model="dForm.baby_data.respitoryEvidenceSpecified"
                            :color="color"
                            auto-grow
                            rows="1"
                            label="Specify Other Respitory Evidence:"
                          ></v-textarea>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field
                            label="Respiratory Rate:"
                            type="number"
                            :color="color"
                            v-model="dForm.baby_data.respiratoryRate"
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field
                            label="Heart Rate:"
                            type="number"
                            :color="color"
                            v-model="dForm.baby_data.heartRate"
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-radio-group
                            dense
                            v-model="dForm.baby_data.murmur"
                            row
                          >
                            <span class="mr-3">Murmur: </span>
                            <v-radio
                              label="Yes"
                              value="Yes"
                              color="success"
                            ></v-radio>
                            <v-radio
                              label="No"
                              value="No"
                              color="success"
                            ></v-radio>
                          </v-radio-group>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-select
                            :items="setupData.abdomen"
                            label="Abdomen:"
                            v-model="dForm.baby_data.abdomen"
                            :color="color"
                            item-color="success"
                            multiple
                          >
                          </v-select>
                        </v-col>
                        <v-col cols="12" sm="6">
                          <v-textarea
                            v-model="dForm.baby_data.abdomenSpecified"
                            :color="color"
                            auto-grow
                            rows="1"
                            label="Specify Other Abdomen:"
                          ></v-textarea>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field
                            type="number"
                            :color="color"
                            v-model="dForm.baby_data.admissionTemp"
                          >
                            <template v-slot:label>
                              Temperature at Admission(<sup>o</sup>C):
                            </template>
                          </v-text-field>
                        </v-col>
                      </v-row>

                      <h5>
                        Initial Diagnosis at Admission:
                        <v-btn
                          title="click to add more fields"
                          icon
                          @click="newInitialDiagnosis"
                          ><v-icon>mdi-plus-box</v-icon></v-btn
                        >
                      </h5>
                      <v-textarea
                        v-for="diag in dForm.baby_data.initialDiagnosis"
                        :key="diag.id"
                        v-model="diag.diagnosis"
                        :color="color"
                        auto-grow
                        rows="1"
                      >
                        <template v-slot:append>
                          <v-btn
                            title="click to remove this field"
                            icon
                            @click="filterInitialDiagnosis(diag)"
                          >
                            <v-icon>mdi-minus-box</v-icon>
                          </v-btn>
                        </template>
                      </v-textarea>

                      <h5>
                        Other Morbidities Developed on Admission:
                        <v-btn
                          title="click to add more fields"
                          icon
                          @click="newMorbidity"
                          ><v-icon>mdi-plus-box</v-icon></v-btn
                        >
                      </h5>
                      <v-textarea
                        v-for="morb in dForm.baby_data.morbidities"
                        :key="morb.id"
                        v-model="morb.morbidity"
                        :color="color"
                        auto-grow
                        rows="1"
                      >
                        <template v-slot:append>
                          <v-btn
                            title="click to remove this field"
                            icon
                            @click="filterMorbidity(morb)"
                          >
                            <v-icon>mdi-minus-box</v-icon>
                          </v-btn>
                        </template>
                      </v-textarea>
                    </v-window-item>

                    <v-window-item :value="2">
                      <h3>Investigations</h3>
                      <v-row>
                        <v-col cols="12" sm="6">
                          <v-text-field
                            label="PCV on Admission(%):"
                            type="number"
                            :color="color"
                            v-model="dForm.baby_data.pcvAdmission"
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6">
                          <v-text-field
                            :color="color"
                            type="number"
                            v-model="dForm.baby_data.rbsAdmission"
                          >
                            <template v-slot:label>
                              RBS on Admission(mmoL/L):
                            </template>
                          </v-text-field>
                        </v-col>
                      </v-row>
                      <v-radio-group
                        dense
                        v-model="dForm.baby_data.fbcResult"
                        row
                      >
                        <span class="mr-3">FBC Result: </span>
                        <v-radio
                          label="N/A"
                          value="None"
                          color="success"
                        ></v-radio>
                        <v-radio
                          label="Yes"
                          value="Yes"
                          color="success"
                        ></v-radio>
                      </v-radio-group>
                      <v-row v-if="dForm.baby_data.fbcResult == 'Yes'">
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field
                            label="PCV(%):"
                            type="number"
                            :color="color"
                            v-model="dForm.baby_data.pcv"
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field
                            type="number"
                            :color="color"
                            v-model="dForm.baby_data.wbc"
                          >
                            <template v-slot:label>
                              WBC Total(x10<sup>9</sup>/L):
                            </template>
                          </v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field
                            label="Neut(%):"
                            type="number"
                            :color="color"
                            v-model="dForm.baby_data.neut"
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field
                            label="Lymph(%):"
                            type="number"
                            :color="color"
                            v-model="dForm.baby_data.lymph"
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field
                            label="Baso(%):"
                            type="number"
                            :color="color"
                            v-model="dForm.baby_data.baso"
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field
                            label="Eosin(%):"
                            type="number"
                            :color="color"
                            v-model="dForm.baby_data.eosin"
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field
                            label="Platelet(%):"
                            type="number"
                            :color="color"
                            v-model="dForm.baby_data.platelet"
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field
                            label="MCV(%):"
                            type="number"
                            :color="color"
                            v-model="dForm.baby_data.mcv"
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field
                            label="MCHC(%):"
                            type="number"
                            :color="color"
                            v-model="dForm.baby_data.mchc"
                          ></v-text-field>
                        </v-col>
                      </v-row>
                      <v-text-field
                        label="Blood Culture Result:"
                        :color="color"
                        v-model="dForm.baby_data.bloodCulture"
                      ></v-text-field>

                      <h5>
                        Other Investigations
                        <v-btn
                          title="click to add more fields"
                          icon
                          @click="initializeInvestigationForm"
                          ><v-icon>mdi-plus-box</v-icon></v-btn
                        >
                      </h5>
                      <v-data-table
                        hide-default-footer
                        :headers="investigationHeaders"
                        :items="dForm.baby_data.otherInvestigations"
                      >
                        <!-- <template v-slot:item.sn="{ item }">
                      {{ index }}
                    </template> -->
                        <template v-slot:item.actions="{ item }">
                          <v-icon
                            small
                            class="mr-2"
                            @click="editInvestigation(item)"
                          >
                            mdi-pencil
                          </v-icon>
                          <v-icon small @click="removeInvestigation(item)">
                            mdi-delete
                          </v-icon>
                        </template>
                      </v-data-table>
                      <v-container v-if="showInvestigationForm">
                        <v-row justify="center" class="white">
                          <v-col cols="12" sm="3">
                            <v-textarea
                              label="Investigation"
                              v-model="newInvestigationForm.investigation"
                              :color="color"
                              auto-grow
                              rows="1"
                            ></v-textarea>
                          </v-col>
                          <v-col cols="12" sm="3">
                            <v-textarea
                              label="Result"
                              v-model="newInvestigationForm.result"
                              :color="color"
                              auto-grow
                              rows="1"
                            ></v-textarea>
                          </v-col>
                          <v-col cols="12" sm="3">
                            <v-text-field
                              v-model="newInvestigationForm.unit"
                              label="Unit"
                              :color="color"
                            ></v-text-field>
                          </v-col>
                          <v-btn
                            text
                            small
                            @click="showInvestigationForm = false"
                            >cancel</v-btn
                          >
                          <v-btn
                            v-if="newInvestigationForm.id"
                            text
                            small
                            @click="updateInvestigation"
                            >update</v-btn
                          >
                          <v-btn v-else text small @click="addInvestigation"
                            >add</v-btn
                          >
                        </v-row>
                      </v-container>
                    </v-window-item>
                    <v-window-item :value="3">
                      <v-row>
                        <v-col cols="12" md="5">
                          <v-radio-group
                            dense
                            v-model="dForm.baby_data.bloodTrans"
                            row
                          >
                            <span class="mr-3">Blood Transfusions: </span>
                            <v-radio
                              label="Yes"
                              value="Yes"
                              color="success"
                            ></v-radio>
                            <v-radio
                              label="No"
                              value="No"
                              color="success"
                            ></v-radio>
                          </v-radio-group>
                        </v-col>
                        <v-col
                          cols="12"
                          sm="5"
                          md="3"
                          v-if="dForm.baby_data.bloodTrans == 'Yes'"
                        >
                          <v-text-field
                            label="Number of Blood Transfusions:"
                            :color="color"
                            type="number"
                            v-model="dForm.baby_data.bloodTransNo"
                          ></v-text-field>
                        </v-col>
                        <v-col
                          cols="12"
                          sm="7"
                          md="4"
                          v-if="dForm.baby_data.bloodTrans == 'Yes'"
                        >
                          <v-textarea
                            label="Indication for Blood Transfusion:"
                            v-model="dForm.baby_data.bloodTransIndication"
                            :color="color"
                            auto-grow
                            rows="1"
                          ></v-textarea>
                        </v-col>
                        <v-col cols="12" md="6">
                          <v-radio-group
                            dense
                            v-model="dForm.baby_data.admissionPhototherapy"
                            row
                          >
                            <span class="mr-3"
                              >Phototherapy during Admission:
                            </span>
                            <v-radio
                              label="Yes"
                              value="Yes"
                              color="success"
                            ></v-radio>
                            <v-radio
                              label="No"
                              value="No"
                              color="success"
                            ></v-radio>
                          </v-radio-group>
                        </v-col>
                        <v-col cols="12" md="6">
                          <v-radio-group
                            dense
                            v-model="dForm.baby_data.admissionSurgery"
                            row
                          >
                            <span class="mr-3">Surgery during Admission: </span>
                            <v-radio
                              label="Yes"
                              value="Yes"
                              color="success"
                            ></v-radio>
                            <v-radio
                              label="No"
                              value="No"
                              color="success"
                            ></v-radio>
                          </v-radio-group>
                        </v-col>
                      </v-row>
                      <h5>
                        Specify Antibiotics during Admission and Duration
                        <v-btn
                          title="click to add more fields"
                          icon
                          @click="initializeAntibioticForm"
                          ><v-icon>mdi-plus-box</v-icon></v-btn
                        >
                      </h5>
                      <v-data-table
                        hide-default-footer
                        :headers="antibioticHeaders"
                        :items="dForm.baby_data.admissionAntibiotics"
                      >
                        <template v-slot:item.actions="{ item }">
                          <v-icon
                            small
                            class="mr-2"
                            @click="editAntibiotic(item)"
                          >
                            mdi-pencil
                          </v-icon>
                          <v-icon small @click="removeAntibiotic(item)">
                            mdi-delete
                          </v-icon>
                        </template>
                      </v-data-table>

                      <v-container v-if="showAntibioticForm">
                        <v-row justify="center" class="white">
                          <v-col cols="12" sm="5">
                            <v-textarea
                              label="Antibiotic"
                              v-model="newAntibioticForm.antibiotic"
                              :color="color"
                              auto-grow
                              rows="1"
                            ></v-textarea>
                          </v-col>
                          <v-col cols="12" sm="4">
                            <v-textarea
                              label="Indication"
                              v-model="newAntibioticForm.indication"
                              :color="color"
                              auto-grow
                              rows="1"
                            ></v-textarea>
                          </v-col>
                          <v-col cols="12" sm="3">
                            <v-text-field
                              v-model="newAntibioticForm.duration"
                              label="Duration (days)"
                              :color="color"
                            ></v-text-field>
                          </v-col>
                          <v-btn text small @click="showAntibioticForm = false"
                            >cancel</v-btn
                          >
                          <v-btn
                            v-if="newAntibioticForm.id"
                            text
                            small
                            @click="updateAntibiotic"
                            >update</v-btn
                          >
                          <v-btn v-else text small @click="addAntibiotic"
                            >add</v-btn
                          >
                        </v-row>
                      </v-container>

                      <h5>
                        Other Medications on Admission and Duration
                        <v-btn
                          title="click to add more fields"
                          icon
                          @click="initializeMedicationForm"
                          ><v-icon>mdi-plus-box</v-icon></v-btn
                        >
                      </h5>
                      <v-data-table
                        hide-default-footer
                        :headers="medicationHeaders"
                        :items="dForm.baby_data.admissionMedications"
                      >
                        <template v-slot:item.actions="{ item }">
                          <v-icon
                            small
                            class="mr-2"
                            @click="editMedication(item)"
                          >
                            mdi-pencil
                          </v-icon>
                          <v-icon small @click="removeMedication(item)">
                            mdi-delete
                          </v-icon>
                        </template>
                      </v-data-table>

                      <v-container v-if="showMedicationForm">
                        <v-row justify="center" class="white">
                          <v-col cols="12" sm="5">
                            <v-textarea
                              label="Medication"
                              v-model="newMedicationForm.medication"
                              :color="color"
                              auto-grow
                              rows="1"
                            ></v-textarea>
                          </v-col>
                          <v-col cols="12" sm="4">
                            <v-textarea
                              label="Indication"
                              v-model="newMedicationForm.indication"
                              :color="color"
                              auto-grow
                              rows="1"
                            ></v-textarea>
                          </v-col>
                          <v-col cols="12" sm="3">
                            <v-text-field
                              v-model="newMedicationForm.duration"
                              label="Duration (days)"
                              :color="color"
                            ></v-text-field>
                          </v-col>
                          <v-btn text small @click="showMedicationForm = false"
                            >cancel</v-btn
                          >
                          <v-btn
                            v-if="newMedicationForm.id"
                            text
                            small
                            @click="updateMedication"
                            >update</v-btn
                          >
                          <v-btn v-else text small @click="addMedication"
                            >add</v-btn
                          >
                        </v-row>
                      </v-container>
                    </v-window-item>
                    <v-window-item :value="4">
                      <h3>Preterm</h3>
                      <v-radio-group
                        dense
                        v-model="dForm.baby_data.preterm"
                        row
                      >
                        <span class="mr-3">Preterm</span>
                        <v-radio
                          label="Yes"
                          value="Yes"
                          color="success"
                        ></v-radio>
                        <v-radio
                          label="No"
                          value="No"
                          color="success"
                        ></v-radio>
                      </v-radio-group>
                      <div v-if="dForm.baby_data.preterm == 'Yes'">
                        <v-row>
                          <v-col cols="12" md="6">
                            <v-text-field
                              v-model="dForm.baby_data.weightRegainAge"
                              label="At what postnatal age is birth weight regained (days)?"
                              :color="color"
                              type="number"
                            ></v-text-field>
                          </v-col>
                          <v-col cols="12" md="6">
                            <v-text-field
                              v-model="dForm.baby_data.oralFeedAge"
                              label="At what postnatal age was oral feeds at 120mls/kg achieved(days)?"
                              :color="color"
                              type="number"
                            ></v-text-field>
                          </v-col>
                        </v-row>
                        <v-row>
                          <v-col cols="12" md="6">
                            <v-radio-group
                              dense
                              v-model="dForm.baby_data.pretermDischarge"
                              row
                            >
                              <span class="mr-3">Discharge</span>
                              <v-radio
                                label="Yes"
                                value="Yes"
                                color="success"
                              ></v-radio>
                              <v-radio
                                label="No"
                                value="No"
                                color="success"
                              ></v-radio>
                            </v-radio-group>
                          </v-col>
                          <v-col
                            cols="12"
                            md="6"
                            v-if="dForm.baby_data.pretermDischarge == 'Yes'"
                          >
                            <v-text-field
                              type="number"
                              v-model="dForm.baby_data.dischargeWeight"
                              label="Weight at discharge (grams):"
                              :color="color"
                            ></v-text-field>
                          </v-col>
                        </v-row>
                        <v-row v-if="dForm.baby_data.pretermDischarge == 'Yes'">
                          <v-col cols="12" md="6">
                            <v-text-field
                              v-model="dForm.baby_data.dischargePostnatalAge"
                              label="Postnatal age at discharge (days):"
                              :color="color"
                              type="number"
                            ></v-text-field>
                          </v-col>
                          <v-col cols="12" md="6">
                            <v-text-field
                              v-model="dForm.baby_data.dischargePCA"
                              label="PCA at discharge:"
                              :color="color"
                            ></v-text-field>
                          </v-col>
                        </v-row>
                      </div>
                    </v-window-item>

                    <v-window-item :value="5">
                      <h5>
                        Final Diagnosis:
                        <v-btn
                          title="click to add more fields"
                          :disabled="dForm.baby_data.finalDiagnosis.length == 2"
                          icon
                          @click="newFinalDiagnosis"
                        >
                          <v-icon>mdi-plus-box</v-icon>
                        </v-btn>
                      </h5>
                      <v-textarea
                        v-for="diag in dForm.baby_data.finalDiagnosis"
                        :key="diag.id"
                        v-model="diag.diagnosis"
                        :color="color"
                        auto-grow
                        rows="1"
                      >
                        <template v-slot:append>
                          <v-btn
                            title="click to remove this field"
                            icon
                            @click="filterFinalDiagnosis(diag)"
                          >
                            <v-icon>mdi-minus-box</v-icon>
                          </v-btn>
                        </template>
                      </v-textarea>
                      <v-row>
                        <v-col cols="12" sm="10" md="8">
                          <v-text-field
                            v-model="dForm.baby_data.durationTillDischarge"
                            type="number"
                            label="Duration of Admission till discharge/death (days):"
                            :color="color"
                            class="text-wrap"
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="2" md="4">
                          <v-select
                            :items="setupData.units"
                            label="Unit:"
                            v-model="dForm.baby_data.durationTillDischargeUnit"
                            :color="color"
                            item-color="success"
                          >
                          </v-select>
                        </v-col>
                      </v-row>
                      <v-select
                        :items="setupData.illnessOutcome"
                        label="Outcome of Illness"
                        v-model="dForm.baby_data.illnessOutcome"
                        :color="color"
                        item-color="success"
                      >
                      </v-select>

                      <v-textarea
                        v-if="dForm.baby_data.illnessOutcome == 'Discharged'"
                        v-model="dForm.baby_data.dischargeMorbidities"
                        :color="color"
                        auto-grow
                        rows="1"
                        label="State Morbidities at Discharge:"
                      ></v-textarea>
                      <div
                        v-else-if="dForm.baby_data.illnessOutcome == 'Death'"
                      >
                        <h5>
                          State Possible Causes of Death:
                          <v-btn
                            title="click to add more fields"
                            :disabled="dForm.baby_data.deathCauses.length == 2"
                            icon
                            @click="newDeathCause"
                          >
                            <v-icon>mdi-plus-box</v-icon>
                          </v-btn>
                        </h5>
                        <v-textarea
                          v-for="cause in dForm.baby_data.deathCauses"
                          :key="cause.id"
                          v-model="cause.cause"
                          :color="color"
                          auto-grow
                          rows="1"
                        >
                          <template v-slot:append>
                            <v-btn
                              title="click to remove this field"
                              icon
                              @click="filterDeathCause(cause)"
                            >
                              <v-icon>mdi-minus-box</v-icon>
                            </v-btn>
                          </template>
                        </v-textarea>
                      </div>
                      <v-textarea
                        v-else-if="
                          dForm.baby_data.illnessOutcome == 'DAMA' ||
                            dForm.baby_data.illnessOutcome == 'Referred'
                        "
                        v-model="dForm.baby_data.outcomeReason"
                        :color="color"
                        auto-grow
                        rows="1"
                        :label="
                          `State Reason(s) for ${dForm.baby_data.illnessOutcome}:`
                        "
                      ></v-textarea>
                      <v-switch
                        v-model="dForm.submitted"
                        label="Close Case"
                        color="success"
                        inset
                        :false-value="0"
                        :true-value="1"
                      ></v-switch>
                    </v-window-item>
                  </v-container>
                </v-window>

                <v-card-actions>
                  <v-btn
                    small
                    :disabled="window === 0"
                    text
                    outlined
                    color="success"
                    @click="window--"
                  >
                    Previous
                  </v-btn>
                  <v-spacer></v-spacer>
                  <v-btn
                    small
                    depressed
                    color="success"
                    :loading="loading"
                    @click="updateAccount(4, 'baby_data')"
                  >
                    Save Changes
                  </v-btn>
                  <v-spacer></v-spacer>
                  <v-btn
                    v-show="window < 5"
                    small
                    :disabled="window === 5"
                    color="success"
                    outlined
                    @click="window++"
                  >
                    Next</v-btn
                  >
                  <v-btn
                    v-show="window === 5"
                    small
                    color="success"
                    depressed
                    to="/"
                  >
                    Close
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-col>
          </v-row>
        </v-stepper-content>

        <v-snackbar
          v-model="snackbar"
          :color="snackColor"
          bottom
          :timeout="5000"
        >
          {{ snackMsg }}

          <template v-slot:action="{ attrs }">
            <v-btn color="red" text v-bind="attrs" @click="snackbar = false">
              <v-icon>mdi-close</v-icon>
              Close
            </v-btn>
          </template>
        </v-snackbar>
      </v-stepper-items>
    </v-stepper>
  </div>
</template>

<script>
import { apiClient, uuid } from "@/services";
import { mapState } from "vuex";

export default {
  name: "NewPatients",
  props: {
    editUser: {
      type: Object,
      default: null,
    },
  },
  data() {
    return {
      e1: 1,
      window: 0,
      color: "#024202",
      cardColor: "#f0f7f0",
      edit: { name: false, hosp_no: false },
      buttons: [1, 2, 3, 4, 5, 6],
      morbidity: 1,
      newAntibioticForm: {},
      showAntibioticForm: false,
      newMedicationForm: {},
      showMedicationForm: false,
      newInvestigationForm: {},
      showInvestigationForm: false,
      loading: false,
      snackbar: false,
      snackMsg: "",
      snackColor: "success",
      rules: { required: (value) => !!value || "Please fill this field." },
      setupData: {
        education: [
          { value: "", text: "Select...", disabled: true },
          "Nil",
          "Primary",
          "Secondary",
          "Tertiary",
          "Postgraduate",
        ],
        healthcare1: [
          { abbr: "", place: "Select...", disabled: true },
          "Primary Health Care",
          "Community Health Care",
          "General Hospital",
          "Private Hospital",
          "Tertiary",
        ],
        healthcare2: [
          { abbr: "", place: "Select...", disabled: true },
          "Primary Health Care",
          "Community Health Care",
          "General Hospital",
          "Private Hospital",
          "Mission House",
          "FETHI",
          "Tertiary",
          "Home",
          "Others",
        ],
        deliveryMode: [
          { abbr: "", mode: "Select...", disabled: true },
          "Spontaneous Vaginal Delivery",
          "Breech Delivery",
          "Instrumental Delivery",
          "Elective Caesarean Section",
          "Emergency Caesarean Section",
        ],
        units: [
          { value: "", text: "Select...", disabled: true },
          "Mins",
          "Hours",
          "Days",
        ],
        complaints: [
          "Poor cry at birth",
          "Yellowness of the eyes",
          "Respiratory difficulty",
          "Fever",
          "Convulsion",
        ],

        illnessOutcome: ["Discharged", "DAMA", "Referred", "Death"],
        exams: [
          "Pallor",
          "Jaundice",
          "Cyanosis",
          "Fever",
          "Oedema",
          "Hypothermia",
        ],

        respitoryEvidence: [
          "Tachypnoea",
          "Subcostal recession",
          "Intercostal recession",
          "Grunting",
          "Stridor",
        ],
        abdomen: [
          "Distended",
          "Hepatomegaly",
          "Splenomegaly",
          "Ballotable kidney",
          "Ascites",
          "Normal",
        ],
      },
      dForm: {
        id: "",
        name: "",
        hosp_no: "",
        mat_demo: { address: "", matEdu: "" },
        mat_pat_hist: {
          patEdu: "",
          anc: "",
          ancPlace: "",
          ancFourVisits: "",
          deliveryMode: "",
        },
        baby_data: {
          dob: new Date().toISOString().substr(0, 10),
          doa: new Date().toISOString().substr(0, 10),
          ofc: "",
          fbcResult: "",
          initialDiagnosis: [],
          finalDiagnosis: [],
          morbidities: [],
          deathCauses: [],
          pcvAdmission: "",
          otherInvestigations: [],
          bloodTrans: "",
          bloodTransNo: 0,
          admissionAntibiotics: [],
          admissionMedications: [],
          preterm: "",
          presentingComplaints: [],
          presentingComplaintsSpecified: "",
          generalExams: [],
          generalExamsSpecified: "",
          abdomenSpecified: "",
          respitoryEvidenceSpecified: "",
        },
      },
      antibioticHeaders: [
        {
          text: "Antibiotic",
          align: "start",
          sortable: false,
          value: "antibiotic",
        },
        {
          text: "Indication",
          sortable: false,
          value: "indication",
        },
        {
          text: "Duration (Days)",
          sortable: false,
          value: "duration",
        },
        {
          text: "",
          sortable: false,
          value: "actions",
        },
      ],
      medicationHeaders: [
        {
          text: "Medication",
          align: "start",
          sortable: false,
          value: "medication",
        },
        {
          text: "Indication",
          sortable: false,
          value: "indication",
        },
        {
          text: "Duration (Days)",
          sortable: false,
          value: "duration",
        },
        {
          text: "",
          sortable: false,
          value: "actions",
        },
      ],
      investigationHeaders: [
        {
          text: "Investigation",
          align: "start",
          sortable: false,
          value: "investigation",
        },
        {
          text: "Result",
          sortable: false,
          value: "result",
        },
        {
          text: "Unit",
          sortable: false,
          value: "unit",
        },
        {
          text: "",
          sortable: false,
          value: "actions",
        },
      ],
    };
  },

  computed: {
    ...mapState(["curUser"]),
    mobile() {
      return this.$vuetify.breakpoint.smAndDown;
    },
    editSteppers() {
      return !!this.dForm.id;
      // return true
    },
    cUser() {
      const { user_id, first_name, last_name } = this.curUser || {};
      return { id: user_id, first_name, last_name };
    },
    csDelivery() {
      return (
        this.dForm.mat_pat_hist.deliveryMode == "Elective Caesarean Section" ||
        this.dForm.mat_pat_hist.deliveryMode == "Emergency Caesarean Section"
      );
    },
  },
  created() {
    if (this.editUser) {
      this.dForm = { ...this.editUser };
    }
  },
  methods: {
    addAntibiotic() {
      this.dForm.baby_data.admissionAntibiotics.push({
        id: uuid(),
        ...this.newAntibioticForm,
      });
      this.showAntibioticForm = false;
    },
    updateAntibiotic() {
      const { id } = this.newAntibioticForm;
      this.dForm.baby_data.admissionAntibiotics = this.dForm.baby_data.admissionAntibiotics.map(
        (val) => (val.id == id ? { ...this.newAntibioticForm } : val)
      );
      this.showAntibioticForm = false;
    },
    editAntibiotic(antibiotic) {
      this.newAntibioticForm = { ...antibiotic };
      this.showAntibioticForm = true;
    },
    removeAntibiotic(antibiotic) {
      const ok = confirm(
        `Are you sure you want to delete ${antibiotic.antibiotic}?`
      );
      if (ok) {
        this.dForm.baby_data.admissionAntibiotics = this.dForm.baby_data.admissionAntibiotics.filter(
          (val) => val.id != antibiotic.id
        );
      }
    },
    initializeAntibioticForm() {
      this.newAntibioticForm = { antibiotic: "", indication: "", duration: 0 };
      this.showAntibioticForm = true;
    },
    addMedication() {
      this.dForm.baby_data.admissionMedications.push({
        id: uuid(),
        ...this.newMedicationForm,
      });
      this.showMedicationForm = false;
    },
    updateMedication() {
      const { id } = this.newMedicationForm;
      this.dForm.baby_data.admissionMedications = this.dForm.baby_data.admissionMedications.map(
        (val) => (val.id == id ? { ...this.newMedicationForm } : val)
      );
      this.showMedicationForm = false;
    },
    editMedication(medication) {
      this.newMedicationForm = { ...medication };
      this.showMedicationForm = true;
    },
    removeMedication(medication) {
      const ok = confirm(
        `Are you sure you want to delete ${medication.medication}?`
      );
      if (ok) {
        this.dForm.baby_data.admissionMedications = this.dForm.baby_data.admissionMedications.filter(
          (val) => val.id != medication.id
        );
      }
    },
    initializeMedicationForm() {
      this.newMedicationForm = { medication: "", indication: "", duration: 0 };
      this.showMedicationForm = true;
    },
    addInvestigation() {
      this.dForm.baby_data.otherInvestigations.push({
        id: uuid(),
        ...this.newInvestigationForm,
      });
      this.showInvestigationForm = false;
    },
    updateInvestigation() {
      const { id } = this.newInvestigationForm;
      this.dForm.baby_data.otherInvestigations = this.dForm.baby_data.otherInvestigations.map(
        (val) => (val.id == id ? { ...this.newInvestigationForm } : val)
      );
      this.showInvestigationForm = false;
    },
    editInvestigation(investigation) {
      this.newInvestigationForm = { ...investigation };
      this.showInvestigationForm = true;
    },
    removeInvestigation(investigation) {
      const ok = confirm(
        `Are you sure you want to delete ${investigation.investigation}?`
      );
      if (ok) {
        this.dForm.baby_data.otherInvestigations = this.dForm.baby_data.otherInvestigations.filter(
          (val) => val.id != investigation.id
        );
      }
    },
    initializeInvestigationForm() {
      this.newInvestigationForm = { investigation: "", result: "", unit: "" };
      this.showInvestigationForm = true;
    },
    createAccount() {
      this.loading = true;
      const { name, hosp_no } = this.dForm;
      if (!name && !hosp_no) {
        this.displayMessage("Name and Hospital Number is required");
        this.loading = false;
        return;
      }

      this.dForm.created_by = this.dForm.modified_by = { ...this.cUser };
      apiClient
        .post("/", this.dForm)
        .then((res) => {
          // console.log (res.data)
          if (res.data.id) {
            this.dForm = { ...res.data };
            this.loading = false;
            this.displayMessage("New account created successfully");
            this.e1 = 2;
          } else {
            this.displayError(`${res.data}`);
          }
        })
        .catch((err) => this.displayError(`${err.message}`));
    },
    updateAccount(step, type) {
      this.loading = true;
      this.dForm.type = type;
      this.dForm.modified_by = { ...this.cUser };
      //  console.log(this.dForm)
      apiClient
        .put("/", this.dForm)
        .then((res) => {
          // console.log (res.data)
          if (res.data.ok) {
            this.loading = false;
            this.edit.name = this.edit.hosp_no = false;
            this.displayMessage();
            this.e1 = step;
          } else {
            this.displayError(`${res.data}`);
          }
        })
        .catch((err) => this.displayError(`${err.message}`));
    },

    displayMessage(
      msg = "Data saved successfully",
      type = "success",
      state = true
    ) {
      this.snackMsg = msg;
      this.snackColor = type;
      this.snackbar = state;
    },
    displayError(msg) {
      this.loading = false;
      // console.error(err.message)
      this.displayMessage(msg, "error");
    },

    newInitialDiagnosis() {
      this.dForm.baby_data.initialDiagnosis.push({ id: uuid(), diagnosis: "" });
    },
    filterInitialDiagnosis(diag) {
      const ok = confirm(`Are you sure you want to delete ${diag.diagnosis}?`);
      if (ok) {
        this.dForm.baby_data.initialDiagnosis = this.dForm.baby_data.initialDiagnosis.filter(
          (curVal) => curVal.id != diag.id
        );
      }
    },

    newFinalDiagnosis() {
      this.dForm.baby_data.finalDiagnosis.push({ id: uuid(), diagnosis: "" });
    },
    filterFinalDiagnosis(diag) {
      const ok = confirm(`Are you sure you want to delete ${diag.diagnosis}?`);
      if (ok) {
        this.dForm.baby_data.finalDiagnosis = this.dForm.baby_data.finalDiagnosis.filter(
          (curVal) => curVal.id != diag.id
        );
      }
    },

    newMorbidity() {
      this.dForm.baby_data.morbidities.push({ id: uuid(), morbidity: "" });
    },
    filterMorbidity(morb) {
      const ok = confirm(`Are you sure you want to delete ${morb.morbidity}?`);
      if (ok) {
        this.dForm.baby_data.morbidities = this.dForm.baby_data.morbidities.filter(
          (curVal) => curVal.id != morb.id
        );
      }
    },

    newDeathCause() {
      this.dForm.baby_data.deathCauses.push({ id: uuid(), cause: "" });
    },
    filterDeathCause(cause) {
      const ok = confirm(`Are you sure you want to delete ${cause.cause}?`);
      if (ok) {
        this.dForm.baby_data.deathCauses = this.dForm.baby_data.deathCauses.filter(
          (curVal) => curVal.id != cause.id
        );
      }
    },
  },
};
</script>
